body {
  margin: 0;
}

.wrapper {
  background-color: #00f;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  width: 100%;
  height: 100vh;
  display: flex;
}

.title {
  color: #fff;
  font-size: 50px;
  font-style: italic;
}

.task-box {
  gap: 30px;
  margin-top: 20px;
  display: flex;
}

.task-box__input {
  border: none;
  border-radius: 10px;
  width: 200px;
  height: 30px;
  padding-inline: 15px;
  font-size: 20px;
}

.task-box__input:focus {
  outline: none;
}

.task-box__btn {
  border: none;
  border-radius: 10px;
  height: 32px;
  padding-inline: 15px;
}

.task-box__btn:hover {
  cursor: pointer;
  color: #fff;
  background-color: #9fa2a5;
  scale: .99;
}

.todos {
  flex-direction: column;
  gap: 20px;
  height: 100vh;
  display: flex;
}

.todoblock {
  position: relative;
}

.todos__item {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 10px;
  width: 250px;
  height: 32px;
  padding-inline: 15px;
  font-size: 20px;
  line-height: 32px;
  display: block;
}

.del {
  cursor: pointer;
  background-color: #ff4040b3;
  background-image: url("bin-icon.d0d6e14d.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 25px 25px;
  border: none;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 3px;
  right: 10px;
}

.del:hover {
  scale: .9;
}

@media (width <= 366px) {
  .wrapper {
    padding-inline: 5px;
  }

  .title {
    text-align: center;
  }

  .task-box__input {
    width: 150px;
  }
}
